import {
  HomePage,
  GenericPage,
  ProfilePage,
  // ClientesPage,
  // MC3Page,
  // JardinPage,
  // PreciosPage,
} from "./pages";
import { withNavigationWatcher } from "./contexts/navigation";

const routes = [
  {
    path: "/home",
    element: HomePage,
  },
  {
    path: "/ventas",
    element: GenericPage,
  },
  {
    path: "/stocks",
    element: GenericPage,
  },
  {
    path: "/locales",
    element: GenericPage,
  },
  {
    path: "/clientes",
    element: GenericPage,
  },
  {
    path: "/productos",
    element: GenericPage,
  },
  {
    path: "/medio_pagos",
    element: GenericPage,
  },
  {
    path: "/profile",
    element: ProfilePage,
  },
];

export default routes.map((route) => {
  return {
    ...route,
    element: withNavigationWatcher(route.element, route.path),
  };
});
