import React, { useState, useEffect } from "react";
import "./styles.scss";
import DataGrid, {
  Pager,
  Paging,
  FilterRow,
  HeaderFilter,
  Editing,
  Lookup,
  Column,
  Grouping,
  GroupPanel,
} from "devextreme-react/data-grid";
import { getAll } from "../../api/services";
import { useNavigation } from "../../contexts/navigation";

export default function Generic() {
  let refGrid = React.useRef();
  const nav = useNavigation();
  const [DS, setDS] = useState([]);
  const [view, setView] = useState();
  const [columnsDS, setColumnsDS] = useState({});
  const cantidad = [];
  for (let index = 0; index < 100; index++) {
    cantidad.push({ nom: index });
  }

  useEffect(() => {
    let _view = nav.navigationData?.currentPath.replace("/", "");
    if (_view && window.location.href.includes(_view)) setView(_view);
  }, [useNavigation()]);

  useEffect(() => {
    if (!view) return;
    getAll(view).then((response) => {
      if (!response?.length) return;
      let cols = {};
      let withOptions = Object.keys(response[0])?.filter((col) => {
        return col?.includes("_id");
      });
      let cant = withOptions.length;
      let count = 0;
      if (cant) {
        withOptions?.map((col) => {
          let table_name = col.replace("_id", "s");
          let field = col.replace("_id", "");
          getAll(table_name).then((newDS) => {
            if (!newDS?.length) return;
            newDS?.forEach((p) => (p.nombre = p.nombre.trim()));
            cols[field] = newDS;
            response.map((x) => {
              let temp = newDS?.find((y) => y.id === x[col])?.nombre.trim();
              if (temp) x[field] = temp;
            });
            count++;
            if (count === cant) {
              setColumnsDS(cols);
              setDS(response);
            }
          });
        });
      } else {
        setDS(response);
      }
    });
  }, [view]);

  if (DS.length)
    return (
      <React.Fragment>
        <div className={"content-block dx-card responsive-paddings"}>
          <h6 className={"content-block"}>{view}</h6>

          <DataGrid
            keyExpr={"id"}
            ref={refGrid}
            className={"dx-card wide-card"}
            dataSource={DS}
            showBorders={false}
            focusedRowEnabled={true}
            defaultFocusedRowIndex={0}
            columnAutoWidth={true}
            columnHidingEnabled={true}
          >
            <Paging defaultPageSize={10} />
            <Pager showPageSizeSelector={true} showInfo={true} />
            <HeaderFilter visible={true} />
            <Editing
              allowUpdating={true}
              allowAdding={true}
              allowDeleting={true}
              mode="popup"
            />
            <Grouping allowGrouping={true} contextMenuEnabled={true} />
            <GroupPanel visible={true} />
            <FilterRow visible={true} />
            {Object.keys(columnsDS).map((col) => {
              if (columnsDS[col].length)
                return (
                  <Column dataField={col}>
                    <Lookup
                      dataSource={columnsDS[col]}
                      valueExpr="nombre"
                      displayExpr="nombre"
                    />
                  </Column>
                );
            })}

            {Object.keys(DS[0]).map((col) => {
              return (
                !col.includes("id") &&
                !Object.keys(columnsDS).find((y) => y === col) && (
                  <Column
                    dataField={col}
                    dataType={col === "fecha" ? "date" : "string"}
                    format={col === "fecha" ? "dd/MM/yyyy" : ""}
                  />
                )
              );
            })}
            {["ventas", "stocks"].includes(view) && (
              <Column dataField={"cantidad"}>
                <Lookup
                  dataSource={cantidad}
                  valueExpr="nom"
                  displayExpr="nom"
                />
              </Column>
            )}
          </DataGrid>
        </div>
      </React.Fragment>
    );
}
